import * as yup from 'yup';

const maxSymbolsValue = 20;

export const validationSchemaRusEng = yup.object({
  rus: yup
    .string()
    .max(maxSymbolsValue, ' 20 !')
    .required(' !')
    .trim(),
  eng: yup
    .string()
    .max(maxSymbolsValue, ' 20 !')
    .required(' !')
    .trim(),
});

export const validationSchemaTranslate = yup.object({
  translateWord: yup
    .string()
    .max(maxSymbolsValue, '  20 !')
    .required(' !')
    .trim(),
});
